@import './variables.css';

.homepage {
  position: relative;
  background: linear-gradient(135deg, #2f3236, #212427);
  min-height: 100vh;
  padding: 3rem 1rem;
  color: #edf0f1;
  overflow: hidden;
}

/* ...nota animasyon keyframeleri aynen korunur... */

.results {
  position: relative; z-index: 1;
  background: var(--color-card);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  padding: 2rem;
  max-width: 900px;
  margin: 2rem auto 0;
  color: var(--color-text);
}

.details p {
  color: var(--color-text-light);
  margin: 0.5rem 0;
}

.music-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 1rem;
  margin-top: 1.5rem;
}

/* Kart genel stili */
.music-card {
  background: rgba(0, 0, 0, 0.4);
  border-radius: var(--radius);
  overflow: hidden;
  position: relative;
  padding-top: 140px; /* preview alanı için boşluk */
}

/* Önizleme (play butonu burada) */
.card-preview {
  position: absolute;
  top: 0; left: 0;
  width: 100%;
  height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.play-btn {
  background: var(--color-secondary);
  border: none;
  border-radius: 50%;
  width: 48px; height: 48px;
  font-size: 1.25rem;
  color: #fff;
  cursor: pointer;
  box-shadow: var(--shadow);
  transition: transform 0.1s;
}

.play-btn:hover {
  transform: scale(1.1);
}

.spotify-embed {
  width: 100%;
  height: 80px;
  border: none;
  display: block;
}
